body {
    margin: 0px;
    background-color: white;
    font-family: 'Roboto', sans-serif;
    justify-content: center;
    align-items: center;
}

.login-form {
    background-color: #5F939A;
    height: 400px;
    width: 400px;
    text-align: center;
    position: relative;
    margin: auto;
    top: 100px;

    border-radius: 10px;
}

.register-form {
    background-color: #31326f;
    height: 580px;
    width: 400px;

    position: relative;
    margin: auto;
    top: 100px;
    box-shadow: 1px 1px 20px 9px rgba(0, 0, 0, 0.39);
    border-radius: 10px;
}

a {
    text-decoration: none;
    color: black;
}
a:hover {
    color: white;
}

.h1 {
    padding-top: 50px;
    color: white;
    font-weight: 300;
}

.l-input {
    display: block;
    margin: auto;
    margin-bottom: 10px;
    height: 48px;
    width: 300px;
    border: none;
    border-radius: 10px;
    padding-left: 20px;
    position: relative;
    top: 20px;
    outline: none;

    font-size: 1.3rem;
}

.button {
    display: block;
    margin: auto;
    border: none;
    border-radius: 10px;
    width: 300px;
    height: 35px;
    margin-bottom: 10px;
    outline: none;
    cursor:pointer;
    padding-top: 9px;

    font-size: 1.2rem;
    font-weight: 300;
    
    
    position: relative;
    top: 50px;

    background-color: #ffc93c;
}

.button:hover {
    background-color: #b68f2c;
    color: white;
}

::placeholder{
    font-size: 1rem;
}



/* product page*/
.product-input {
    width: 400px;
    position: relative;
    top: 100px;
    box-shadow: 1px 1px 20px 3px rgba(0, 0, 0, 0.39);
}
.search-icon-btn {
    height: 40px;
    width: 40px;
    position: relative;
    left: 175px;
    top: 48px;
    outline: none;
    border: none;
    background-color:transparent;
    cursor: pointer;
    font-size: 25px;
}

.search-icon-btn:hover{
    background-color:transparent;
    font-size: 33px;
}

/* heading */

.company-name {
    margin: 9px 0;
    font-size: 1.9rem;
    font-weight: 700;
}

.logo-card {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 10%;
    max-height: 60px;
    background-color: rosybrown;
    box-shadow: 1px 1px 20px 9px rgba(0, 0, 0, 0.39);
}

.medicine-logo {
    height: 3rem;
    width: 3rem;
    margin: 0px 10px;
}

@media (max-width: 520px) {
    .medicine-logo  {
        height: 2.3rem;
        width: 2.3rem;
        margin: 0px 10px;
    }
    .company-name {
        margin: 9px 0;
        font-size: 1.3rem;
        font-weight: 700;
    }
}

@media (max-width: 375px) {
    .medicine-logo  {
        height: 2.2rem;
        width: 2.2rem;
        margin: 0px 07px;
    }
    .company-name{
