/*medicine*/

#medicine {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 80%;
    box-shadow: 1px 1px 20px 4px rgba(0, 0, 0, 0.39);
    margin: auto;
    margin-top: 190px ;
    margin-bottom: 100px;
  }
  
  #medicine td, #medicine th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #medicine tr:hover {background-color: #ddd;}
  
  #medicine th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #31326f;
    color: white;
  }

  /*Add btn*/

  .add-btn {
    position: relative;
    top: 130px;
    margin: auto;
    margin-right: 30px;
    border: none;
    border-radius: 10px;
    width: 190px;
    height: 32px;
    outline: none;
    cursor:pointer;
    padding: 9px;
    box-shadow: 1px 1px 10px 2px rgba(0, 0, 0, 0.39);
    font-size: 1.2rem;
    font-weight: 300;

    background-color: #ffc93c;
  }


  /*registration form*/

.r-body {
    padding: 10px;
}

.r-container {
    background-color: #5F939A;
    margin: auto;
    margin-top: 50px;
    margin-bottom: 50px;
    max-width: 800px;
    width: 100%;
    border-radius: 10px;
    padding-bottom: 10px;
}

.r-container form {
    padding: 0px 30px;
}

.r-container .r-title {
    padding: 20px 30px;
    font-size: 2rem;
    font-weight: 500;
    position: relative;
}


.r-container form .r-user-details {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 20px 0 12px 0;
}

form .r-user-details .r-input-box{
    margin-bottom: 15px;
    width: calc(100% / 2 - 20px); 
}

/*Select input*/
.r-user-details .r-input-box select{
    height: 45px;
    width: 100%;
    outline: none;
    border-radius: 5px;
    background: white;
    border: 1px solid #ccc;
    padding-left: 15px;
    border-bottom-width: 3px;
    transition: all 0.3 ease;
}

.r-input-box .r-option option {
    color: red;
}

.r-user-details .r-input-box input{
    height: 45px;
    width: 100%;
    outline: none;
    border-radius: 5px;
    background: white;
    border: 1px solid #ccc;
    padding-left: 15px;
    border-bottom-width: 3px;
    transition: all 0.3 ease;
}


::placeholder{ 
    font-size: 18px;
}
.r-input {
    font-size: 20px;
}

.r-user-details .r-input-box .r-details{
    display: block;
    font-weight: 500;
    font-size: 18px;
    padding-bottom: 10px;
}

.r-user-details .r-input-box input:focus,
.r-user-details .r-input-box input:valid {
    border-color: black;
}

form .r-gender-details .r-gender-title {
    font-size: 20px;
    font-weight: 500;
}

form .r-gender-details .r-catagory {
    display: flex;
    width: 70%;
    margin: 14px 0;
    justify-content: space-between;
}

.r-gender-details .r-catagory label {
    display: flex;
    align-items: center;
}

.r-gender-details .r-catagory .r-dot{
    height: 15px;
    width: 15px;
    background:white;
    border-radius: 50%;
    margin-right: 10px;
    border: 5px solid transparent;
    transition: all 0.3s ease;
}
#dot-1:checked ~ .r-catagory label .one,
#dot-2:checked ~ .r-catagory label .two,
#dot-3:checked ~ .r-catagory label .three {
    border-color: #d9d9d9;
    background: #5F939A;
}

form input[type="radio"] {
    display: none;
}

 form .r-button {
     height: 45px;
     margin: 45px 0;
 }

 form .r-reg-btn {
    height: 45px;
    margin: 20px 0;
}

 form .r-button input {
     height: 100%;
     width: 100%;
     outline: none;
     border: none;;
     font-size: 20px;
     font-weight: 500;
     border-radius: 5px;
     letter-spacing: 1px;
     background: white;
 }

 form .r-reg-btn input {
    height: 100%;
    width: 100%;
    outline: none;
    border: none;;
    font-size: 20px;
    font-weight: 500;
    border-radius: 5px;
    letter-spacing: 1px;
     background-color: #ffc93c;
 }

 form .r-button input:hover {
    background: #ffc93c;
 }

 form .r-reg-btn input:hover {
     background-color: white;
 }

 @media (max-width: 584px) {
     .r-container {
         max-width: 100%;
     }
     .r-container .r-title {
         font-size: 1.8rem;
     }
     form .r-user-details .r-input-box{
         margin-bottom: 15px;
         width: 100%;
     }

     form .r-gender-details .r-catagory {
         width: 80%;
         font-size: 0.9rem;
     }

     .r-container form .r-user-details {
         max-height: 600px;
         overflow-y: scroll;
     }
     .r-gender-details .r-catagory label {
        margin: 0 10px;
    }

     .r-user-details::-webkit-scrollbar {
         width: 0;
     }
     .r-container {
         margin-top: 200px;
     }
     .statement-container {
        margin-top: 500px;
    }
    ::placeholder{ 
        font-size: 0.9rem;
    }

 }


